<template>
  <Header title="常見問題" />
  <div class="faq">
    <section class="c-main">
      <div class="list-container">
        <div class="mb-3 kind-container">
          <div class="title"><i class="fas fa-question"></i>雇主專區</div>
        </div>
        <div class="accordion accordion-faq" id="accordionFaq">
          <div class="accordion-item">
            <div class="accordion-header">
              <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                data-bs-target="#collapse1">看護銀行APP提供什麼服務？</button>
            </div>
            <div id="collapse1" class="accordion-collapse collapse" data-bs-parent="#accordionFaq">
              <div class="accordion-body">
                <div class="answer-container">
                  <p class="text">
                    看護銀行APP提供一個線上媒合平台，在您有照護需求時不必受限於時間限制到處撥打電話，可立即將您的需求在平台上發布，或者在平台上搜尋合適的照護人員。<br>
                    目前可選擇的照護類型有<strong>看護、護理師、物理治療師、職能治療師、心理治療師</strong>，您可到<strong>會員專區->服務需求資料->右上角新增->選擇服務類別</strong>，參考各照護人員所提供的服務。
                  </p>
                </div>
              </div>
            </div>
          </div>
          <div class="accordion-item">
            <div class="accordion-header">
              <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                data-bs-target="#collapse2">看護主要提供什麼服務？</button>
            </div>
            <div id="collapse2" class="accordion-collapse collapse" data-bs-parent="#accordionFaq">
              <div class="accordion-body">
                <div class="answer-container">
                  <p class="text">
                    看護服務主要分為八大需求：
                  </p>
                  <ul>
                    <li><strong>身體清潔需求</strong>：攙扶入浴、擦澡、床上洗頭、洗臉刷牙、刮鬍子(需有刮鬍刀)、修剪指甲(不處理灰指甲、凍甲)、大小便清潔(含尿布更換、失禁處理) 。</li>
                    <li><strong>陪病需求</strong>：代取慢性處方籤藥物、協助掛號(含預約)、陪同就診及轉知醫囑與注意事項。</li>
                    <li><strong>用餐需求</strong>：備餐、加熱食物、餵食、代購餐食。</li>
                    <li><strong>管路需求</strong>：管灌飲食(含沖泡管灌用飲品)、清潔管路(含鼻胃管、導尿管、腸胃造廔口)、倒尿袋。</li>
                    <li><strong>身體照顧需求</strong>：此服務特針對臥床病患提供，翻身拍背、移位、協助上下床、肢體關節活動(非按摩)、大小便處理(含換尿布、尿套、倒尿桶等)。</li>
                    <li><strong>跑腿需求</strong>：代購物品(生鮮食品、生活用品)、代辦事項(郵寄、代領)</li>
                    <li>
                      <strong>心靈需求</strong>：陪同外出活動(如購物、社交活動、辦理事務、參與宗教活動、用餐、散步、定期式復健或洗腎、運動等。)、陪同室內活動(如音樂陪伴、簡易體操、懷舊活動等)、心靈陪伴、書報及文件代讀。
                    </li>
                    <li><strong>其他需求</strong>：污物清潔(衣服、床被單)、照顧技巧指導(針對外籍看護或家屬)</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
          <div class="accordion-item">
            <div class="accordion-header">
              <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                data-bs-target="#collapse3">看護是什麼國籍？</button>
            </div>
            <div id="collapse3" class="accordion-collapse collapse" data-bs-parent="#accordionFaq">
              <div class="accordion-body">
                <div class="answer-container">
                  <p class="text">
                    看護銀行的照護人員大部分是台灣人，部分是已取得永久居留權或身分證的外籍配偶，所有看護都經過政府的照服員訓練合格並核發證照，請安心預約。<br>
                    若客戶希望是會講台語、客家語、英語或其他語言的服務人員，可於<strong>新增服務需求</strong>時勾選。
                  </p>
                </div>
              </div>
            </div>
          </div>
          <div class="accordion-item">
            <div class="accordion-header">
              <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                data-bs-target="#collapse4">平台上的照護人員都有合格證照嗎？</button>
            </div>
            <div id="collapse4" class="accordion-collapse collapse" data-bs-parent="#accordionFaq">
              <div class="accordion-body">
                <div class="answer-container">
                  <p class="text">本平台的照護人員皆需上傳專業合格證照並審核通過，看護並提供良民證與體檢表，請安心預約。</p>
                </div>
              </div>
            </div>
          </div>
          <div class="accordion-item">
            <div class="accordion-header">
              <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                data-bs-target="#collapse5">看護可以幫忙做家事嗎？</button>
            </div>
            <div id="collapse5" class="accordion-collapse collapse" data-bs-parent="#accordionFaq">
              <div class="accordion-body">
                <div class="answer-container">
                  <p class="text">因看護的工作內容以身體照顧為主，非家事專業，家事服務範圍僅限照顧對象的基本清潔維護；備餐最多只能準備雇主與被照顧者兩人的餐食。</p>
                </div>
              </div>
            </div>
          </div>
          <div class="accordion-item">
            <div class="accordion-header">
              <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                data-bs-target="#collapse6">如果看護服務品質不佳怎麼辦？</button>
            </div>
            <div id="collapse6" class="accordion-collapse collapse" data-bs-parent="#accordionFaq">
              <div class="accordion-body">
                <div class="answer-container">
                  <p class="text">
                    您可在<strong>看護頁面</strong>點選星星留下評價，日後平台上就能參考您的評分，讓評分不佳的人員落在搜尋順位底下；如情節嚴重您可來電0800-232088，由專員協助處理。</p>
                </div>
              </div>
            </div>
          </div>
          <div class="accordion-item">
            <div class="accordion-header">
              <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                data-bs-target="#collapse7">雇主可與看護私下交易？</button>
            </div>
            <div id="collapse7" class="accordion-collapse collapse" data-bs-parent="#accordionFaq">
              <div class="accordion-body">
                <div class="answer-container">
                  <p class="text">
                    為了保障雙方的權益，使用平台預約照服人員均有合約規範，交易日期、時間及費用將由平台紀錄，減少交易糾紛產生；若非經本平台私下交易，看護銀行恕不能就雙方交易聯繫協調、取消、退費…等。</p>
                </div>
              </div>
            </div>
          </div>
          <div class="accordion-item">
            <div class="accordion-header">
              <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                data-bs-target="#collapse8">我輸入的被照顧者資料安全嗎？會不會被挪作它用？</button>
            </div>
            <div id="collapse8" class="accordion-collapse collapse" data-bs-parent="#accordionFaq">
              <div class="accordion-body">
                <div class="answer-container">
                  <p class="text">您輸入的資料與服務需求僅在平台上用作派工資訊，僅供已註冊並審核通過的照護人員瀏覽，平台不會洩漏個資予他方。</p>
                </div>
              </div>
            </div>
          </div>
          <div class="accordion-item">
            <div class="accordion-header">
              <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                data-bs-target="#collapse9">如果看護未到班怎麼辦？</button>
            </div>
            <div id="collapse9" class="accordion-collapse collapse" data-bs-parent="#accordionFaq">
              <div class="accordion-body">
                <div class="answer-container">
                  <p class="text">若照護人員無故未到，費用將全額退還。</p>
                </div>
              </div>
            </div>
          </div>
          <div class="accordion-item">
            <div class="accordion-header">
              <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                data-bs-target="#collapse10">使用平台上的服務可以申請公費補助嗎？</button>
            </div>
            <div id="collapse10" class="accordion-collapse collapse" data-bs-parent="#accordionFaq">
              <div class="accordion-body">
                <div class="answer-container">
                  <p class="text">可以，如果具備補助資格請來電0800-232088客服中心申請收據，會依照平台上日期與費用開立。</p>
                </div>
              </div>
            </div>
          </div>
          <!-- <div class="accordion-item">
            <div class="accordion-header">
              <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                data-bs-target="#collapse11">使用這個平台需要付費嗎？</button>
            </div>
            <div id="collapse11" class="accordion-collapse collapse" data-bs-parent="#accordionFaq">
              <div class="accordion-body">
