            </div>
            <div id="collapse13" class="accordion-collapse collapse" data-bs-parent="#accordionFaq">
              <div class="accordion-body">
                <div class="answer-container">
                  <p class="text">
                    付款前都可以取消預約，不會有手續費。<br>
                    付款後若有任何原因須取消派班，為了保障服務人員的權益，將酌收手續費:
                  </p>
                  <ul>
                    <li>約定的服務時間24小時到72小時內取消，扣除照護費用的30%</li>
                    <li>約定的服務時間2小時到24小時內取消，扣除照護費用的50%</li>
                    <li>約定的服務時間2小時內取消 扣除照護費用的100%</li>
                  </ul>
                  <p class="text">
                    <br>
                    如遇不可抗力因素需取消，如:往生、急診、進加護病房…等，請聯繫客服人員處理。
                  </p>
                </div>
              </div>
            </div>
          </div>
